@import "../common.less";

.page {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;

  margin-left: auto;
  margin-right: auto;

  -webkit-box-shadow: @shadow-boder;
  -moz-box-shadow: @shadow-boder;
  box-shadow: @shadow-boder;

  width: 75%;
  min-height: calc(100vh - 80px);

  background-color: @color-page;

  .offset-line {
    width: 20px;
  }

  .left-line {
    width: 30%;
    min-width: 250px;
    margin-top: 20px;
    display: flex;
    flex-flow: column;

    .photo {
      width: 125px;
      height: 125px;
      margin-left: auto;
      margin-right: auto;
      margin-bottom: 20px;
      border: 1px solid black;
    }

    .left-color-line {
      width: 100%;
      flex-grow: 1;
      min-height: inherit;
      height: max-content;
      background-color: @color-left;
    }
  }

  .main-line {
    width: 70%;
    height: 800px;
  }
}
